@font-face {
  font-family: "Oswald";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff") format("woff"),
    url("../fonts/oswaldregular.woff2") format("woff2");
}

@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff") format("woff"),
    url("../fonts/oswaldmedium.woff2") format("woff2");
}

body {
  min-width: 320px;
  font-family: "Oswald", "Helvetica", sans-serif;
}

.text-font {
  font-family: "Arial", "Helvetica", sans-serif;
}

.container {
  width: 280px;
  padding: 0 20px;
  margin: auto;

  @media (min-width: $tablet-width) {
    margin: auto;
    width: 708px;
    padding: 0 30px;
  }

  @media (min-width: $desktop-width) {
    margin: auto;
    width: 1220px;
    padding: 0 110px;
  }
}

